export default {
	components: {

	},
	data() {
		return {
			item: {}
		}
	},
	onLoad: function(options) {
		console.log(options.id);
		uniCloud.callFunction({
				name: 'group-get-by-docId',
				data: {
					id: options.id
				}
			})
			.then(res => {
				console.log("success with" + JSON.stringify(res));
				this.item = res.result.data[0];
			});
	},
	onShareAppMessage(res) {
		if (res.from === 'button') { // 来自页面内分享按钮
			console.log(res)
		}
		return {
			title: this.item.name,
			path: '/pages/group/group-view?id=' + this.item._id,
			imageUrl: this.item.imgId,
			// desc: this.item.description,
			success: function() {
				console.log("share success")
			},
			fail: function() {
				console.log("share fail")
			},
			complete: function() {
				console.log("share complete")
			}
		}
	},
	methods: {
		inviteFriend(e) {
			console.log("start to invite friends to %s", e._id)
		}
	}
}
